:root {
  --background: #0a0a0a;
  --foreground: #ededed;

  /* Brand Colors - Vibrant & Professional */
  --primary: #a855f7;
  /* Purple 500 */
  --primary-dark: #7e22ce;
  --accent: #06b6d4;
  /* Cyan 500 */
  --success: #10b981;
  /* Emerald 500 */
  --warning: #f59e0b;
  /* Amber 500 */
  --danger: #ef4444;
  /* Red 500 */

  /* Safe Area Variables for In-App Feel */
  --safe-area-top: env(safe-area-inset-top, 0px);
  --safe-area-bottom: env(safe-area-inset-bottom, 0px);
  --safe-area-left: env(safe-area-inset-left, 0px);
  --safe-area-right: env(safe-area-inset-right, 0px);

  /* UI Colors */
  --surface: #171717;
  --surface-highlight: #262626;
  --border: rgba(255, 255, 255, 0.1);
  --text-primary: #ffffff;
  --text-secondary: #a3a3a3;
  --text-muted: #737373;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {
  max-width: 100vw;
  overflow-x: hidden;
  background-color: var(--background);
  color: var(--text-primary);
  font-family: 'Outfit', sans-serif;

  /* Mobile App Feel */
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
       user-select: none;
  overscroll-behavior-y: none;
  /* Prevent pull-to-refresh unless desired */
}

/* Allow selection in inputs & Prevent iOS Zoom */
input,
textarea,
select {
  -moz-user-select: text !important;
       user-select: text !important;
  -webkit-user-select: text !important;
}

@media (max-width: 768px) {

  input,
  select,
  textarea {
    font-size: 16px !important;
    /* Prevents auto-zoom on iOS */
  }
}

a {
  color: inherit;
  text-decoration: none;
}

/* Glassmorphism Utilities */
.glass-card {
  background: var(--bg-card);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  transition: all 0.3s ease;
}

.glass-card:hover {
  background: var(--bg-card-hover);
  border-color: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.3);
}

/* Typography */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  letter-spacing: -0.02em;
}

.text-gradient {
  background: linear-gradient(to right, var(--primary), var(--accent));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.text-glow {
  text-shadow: 0 0 20px var(--primary-glow);
}

/* Layout */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Mobile optimizations */
@media (max-width: 768px) {
  .container {
    padding: 0 12px;
  }

  .glass-card {
    border-radius: 12px;
  }

  h1 {
    font-size: 1.75rem;
  }

  h2 {
    font-size: 1.25rem;
  }
}

.grid-auto {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 24px;
}

/* Buttons */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 12px 24px;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
}

.btn-primary {
  background: var(--primary);
  color: #000;
  box-shadow: 0 0 20px var(--primary-glow);
}

.btn-primary:hover {
  transform: scale(1.05);
  box-shadow: 0 0 30px var(--primary-glow);
}

.btn-outline {
  background: transparent;
  border: 1px solid var(--primary);
  color: var(--primary);
}

.btn-outline:hover {
  background: rgba(0, 255, 157, 0.1);
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes pulse {

  0%,
  100% {
    opacity: 1;
  }

  50% {
    opacity: 0.5;
  }
}

.fade-in {
  animation: fadeIn 0.5s ease-out;
}

.animate-pulse {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

/* Skeleton loader */
.skeleton {
  background: linear-gradient(90deg,
      rgba(255, 255, 255, 0.05) 25%,
      rgba(255, 255, 255, 0.1) 50%,
      rgba(255, 255, 255, 0.05) 75%);
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite;
}

@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }

  100% {
    background-position: 200% 0;
  }
}

@keyframes slideInLeft {
  from {
    width: 0;
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes slideInRight {
  from {
    width: 0;
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Aurora Background Animation */
@keyframes aurora {
  0% {
    background-position: 50% 50%, 50% 50%;
  }

  50% {
    background-position: 100% 50%, 0% 50%;
  }

  100% {
    background-position: 50% 50%, 50% 50%;
  }
}

.animate-aurora {
  background-image:
    radial-gradient(at 0% 0%, rgba(168, 85, 247, 0.15) 0px, transparent 50%),
    radial-gradient(at 100% 0%, rgba(6, 182, 212, 0.15) 0px, transparent 50%);
  background-size: 100% 100%;
  animation: aurora 15s ease infinite alternate;
}

/* Floating Animation */
@keyframes float {
  0% {
    transform: translateY(0px);
  }

  50% {
    transform: translateY(-20px);
  }

  100% {
    transform: translateY(0px);
  }
}

.animate-float {
  animation: float 6s ease-in-out infinite;
}

.animate-float-delayed {
  animation: float 7s ease-in-out infinite 1s;
  /* Delayed start */
}

/* Brutal Glass Enhancement */
.glass-brutal {
  background: rgba(20, 20, 20, 0.6);
  backdrop-filter: blur(24px);
  -webkit-backdrop-filter: blur(24px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.5), 0 20px 50px rgba(0, 0, 0, 0.5);
}

.text-super-glow {
  text-shadow: 0 0 30px rgba(168, 85, 247, 0.5), 0 0 60px rgba(168, 85, 247, 0.3);
}

/* Accordion Smooth Transition */
.accordion-content {
  display: grid;
  grid-template-rows: 0fr;
  transition: grid-template-rows 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.accordion-content.expanded {
  grid-template-rows: 1fr;
}

.accordion-inner {
  overflow: hidden;
}

/* Premium Gradient Border */
.premium-border {
  position: relative;
  border-radius: inherit;
}

.premium-border::before {
  content: "";
  position: absolute;
  inset: -2px;
  background: linear-gradient(45deg, var(--primary), var(--accent), var(--primary));
  background-size: 200% 200%;
  border-radius: inherit;
  z-index: -1;
  animation: borderRotate 4s linear infinite;
  opacity: 0.3;
}

@keyframes borderRotate {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

/* Pulse Animation for Live Games */
.live-pulse {
  position: relative;
}

.live-pulse::after {
  content: "";
  position: absolute;
  inset: -1px;
  border: 1px solid #ef4444;
  border-radius: inherit;
  animation: livePulse 2s cubic-bezier(0.24, 0, 0.38, 1) infinite;
}

@keyframes livePulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }

  100% {
    transform: scale(1.3);
    opacity: 0;
  }
}

/* App-Like Utility Classes */
.pb-safe {
  padding-bottom: var(--safe-area-bottom);
}

.pt-safe {
  padding-top: var(--safe-area-top);
}

.mobile-haptic:active {
  transform: scale(0.96);
  transition: transform 0.1s ease;
}

@media (max-width: 768px) {
  .hide-on-mobile {
    display: none !important;
  }
}

/* Hide Scrollbar but allow scrolling */
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

/* Smooth Horizontal Scroll */
.scroll-smooth-x {
  overflow-x: auto;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
}
